
$(function () {
    document_ready();
    window_onload();
    aos_init();
});

function document_ready() {
    ts = (new Date()).getTime();

    $("body").removeClass("no-transition");
    $('body')[0].offsetHeight;
    $("body").addClass("ready");

    responsive_init();
    init_event_handlers();
    bind_widgets();

    te = (new Date()).getTime();
    console.log("On-Ready Load Time: ", te - ts);
}

function window_onload() {
    ts = (new Date()).getTime();
    $("body").removeClass("not-loaded").addClass("loaded");

    responsive_update();

    te = (new Date()).getTime();
    console.log("Window Load Time: ", te - ts);
}

$(window).scroll(function () {
});

$(window).on("resize", function (e) {
    responsive_update(null, true);
});

function init_event_handlers() {
    blazy_init();
    goto_init();
    fix_100vh_init();
    fix_touch_hovers();
    click_touch_init();
    form_process_init();
    toggle_element_init();
    nouislider_init();
    misc_init();
}

function bind_widgets(context) {
    img_to_bg(context);
    validate_init();
    fancybox_init(context);
    checkbox_plain_init(null, context);
    mask_init(context);
}